body {
  background: #fff;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #1c1c1c;
}

.container {
  max-width: 1440px;
  padding: 0 16px;
  margin: 0 auto;
  box-sizing: content-box;
}

.btn__wht {
  border: 2px solid #edde3f;
  border-radius: 12px;
  padding: 20px 40px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #1c1c1c;
  box-shadow: 0 4px 4px -1px rgba(12, 12, 13, 0.05), 0 4px 4px -1px rgba(12, 12, 13, 0.1);
  background: #fff;
  transition: all 0.3s;
  display: block;
}
.btn__wht:hover {
  background: #edde3f;
}
.btn__wht:active {
  background: #1c1c1c;
  box-shadow: none;
  color: #fff;
  border-color: #1c1c1c;
}
.btn__blc {
  border: none;
  border-radius: 12px;
  padding: 22px 42px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
  box-shadow: 0 4px 4px -1px rgba(12, 12, 13, 0.05), 0 4px 4px -1px rgba(12, 12, 13, 0.1);
  background: #1c1c1c;
  transition: all 0.3s;
  display: block;
}
.btn__blc:hover {
  background: #edde3f;
  color: #1c1c1c;
}
.btn__blc:active {
  box-shadow: none;
}

.nav {
  box-shadow: 0 4px 10px 0 rgba(204, 204, 204, 0.8);
  background: #1c1c1c;
  padding: 6px 0;
}
.nav__hum, .nav__phone {
  display: none;
}
.nav .container {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.nav__logo {
  max-width: 148px;
  width: 100%;
}
.nav__logo img {
  width: 100%;
}
.nav__list {
  margin: 0 0 0 auto;
  gap: 60px;
  display: flex;
}
.nav__list a {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
  transition: all 0.3s;
}
.nav__list a:hover {
  color: #edde3f;
}
.nav__soc {
  display: flex;
  gap: 16px;
}
.nav__soc a {
  display: none;
}
.nav__soc a:nth-child(1), .nav__soc a:nth-child(2) {
  display: block;
}
.nav__soc svg {
  width: 40px;
  height: 40px;
}

.header {
