.help {
  padding: 0 0 280px;
}
.help__title {
  padding: 60px 0 36px;
  position: relative;
  z-index: 2;
}
.help__title::before {
  content: "";
  display: block;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
