  font-size: 30px;
  line-height: 1.2;
  color: #1c1c1c;
}
.help__wrapper {
  position: relative;
  z-index: 2;
  padding: 64px 20px 100px 0;
  width: 50%;
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
}
.help__wrapper::before {
  content: "";
  display: block;
  z-index: -1;
  position: absolute;
  top: 0;
  left: 100%;
  transform: translateX(-50%);
  width: 100vw;
  height: calc(100% + 100px);
  background: #f2f2f2;
}
.help__wrapper::after {
  content: "";
  display: block;
  z-index: -1;
  position: absolute;
  top: 0;
  right: 0;
  width: 50vw;
  height: 100%;
  background: #1c1c1c;
}
.help__btn {
