  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  color: #1c1c1c;
  max-width: 447px;
}

.case {
  padding: 160px 0 0;
  position: relative;
  overflow: hidden;
}
.case h2 {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 40px;
  line-height: 1.2;
  color: #1c1c1c;
}
.case__slider {
  margin: 40px 0 0;
  overflow: visible;
}
.case__slide {
  max-width: 1070px;
  border: 2px solid #edde3f;
  box-shadow: 0 4px 4px -1px rgba(12, 12, 13, 0.05), 0 4px 4px -1px rgba(12, 12, 13, 0.1);
  background: #fff;
  padding: 40px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  row-gap: 50px;
}
.case__slide p {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.16667;
  color: #1c1c1c;
}
.case__slide p + p {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.2;
  text-align: right;
  color: #1c1c1c;
}
.case__slide .btn__blc {
  grid-column: 2/3;
  max-width: 345px;
}
.case__list p {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.2;
  color: #1c1c1c;
}
.case__list ul {
  display: grid;
  gap: 28px;
  margin: 30px 0 0;
}
.case__list ul li {
  position: relative;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22222;
  color: #1c1c1c;
  padding: 0 0 20px 28px;
  border-bottom: 1px solid #b9b9b9;
}
.case__list ul li::before {
  content: "";
  display: block;
  width: 16px;
  height: 16px;
  border-radius: 2px;
  background: #edde3f;
  position: absolute;
  left: 0;
  top: 5px;
}
.case__arrow {
  margin: 30px 0 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
}
.case__arrow .swiper-button-next,
.case__arrow .swiper-button-prev {
  position: relative;
  inset: auto;
  transform: none;
  margin: 0;
  width: 60px;
  height: 60px;
  border-radius: 100%;
  transition: all 0.3s;
  background: #edde3f;
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.25);
}
.case__arrow .swiper-button-next svg,
.case__arrow .swiper-button-prev svg {
  width: 26px;
  height: 16px;
}
.case__arrow .swiper-button-next::after,
.case__arrow .swiper-button-prev::after {
  content: none;
}
.case__arrow .swiper-button-next:hover,
.case__arrow .swiper-button-prev:hover {
  box-shadow: none;
  background: #d9d9d9;
}
.case__arrow .swiper-button-next.swiper-button-disabled,
.case__arrow .swiper-button-prev.swiper-button-disabled {
  background: #d9d9d9;
  opacity: 1;
}
.case__footer {
  margin: 53px 0 0;
  padding: 60px 0;
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr;
  gap: 24px;
  align-items: center;
}
.case__footer span {
  padding: 11px;
  background: #edde3f;
}
.case__footer span svg {
  width: 87px;
}
.case__footer::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  background: #1c1c1c;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  z-index: -1;
}
.case__footer p {
  font-family: "Raleway", sans-serif;
  font-weight: 700;
  font-size: 30px;
  line-height: 1.2;
  color: #fff;
  max-width: 1000px;
}

.rate {
  padding: 160px 0 80px;
}
.rate h2 {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 40px;
  line-height: 1.2;
  color: #1c1c1c;
}
.rate__subtitle {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  color: #1c1c1c;
  margin: 40px 0 0;
}
.rate__wrapper {
  margin: 24px 0 0;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  gap: 20px;
}
.rate__item {
  display: flex;
