}
.rate__item_title {
  padding: 22px;
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.16667;
  text-align: center;
  color: #1c1c1c;
  z-index: 2;
  margin: 0 -20px;
  background: #edde3f;
}
.rate__item_text {
  margin: 36px 22px 0;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.25;
  color: #1c1c1c;
}
.rate__item ul {
  margin: 36px 22px auto;
}
.rate__item ul li {
  position: relative;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22222;
  color: #1c1c1c;
}
.rate__item ul li::before {
  content: "";
  position: absolute;
  display: block;
  width: 4px;
  height: 4px;
  border-radius: 100%;
  top: 10px;
  left: -15px;
  background: #1c1c1c;
}
.rate__item .btn__blc {
  margin: 30px 0 0;
}

.ready {
  background: #f2f2f2;
  position: relative;
  overflow: hidden;
}
.ready__title {
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 40px;
  line-height: 1.2;
  color: #1c1c1c;
  padding: 50px 26px 50px 0;
  position: relative;
  z-index: 2;
}
.ready__title::before {
  content: "";
  display: block;
  width: 100vw;
  height: 100%;
  right: 0;
  top: 0;
  background: #edde3f;
  z-index: -1;
  position: absolute;
}
.ready__wrapper {
  padding: 27px 20px 68px;
  max-width: 711px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}
.ready__wrapper p {
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  color: #1c1c1c;
  grid-column: 1/3;
}
.ready__wrapper input {
  width: 100%;
  box-sizing: border-box;
  max-width: 344px;
  border: 1px solid #b9b9b9;
  padding: 18px 20px;
  background: #fff;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 20px;
  line-height: 1.2;
  color: #b9b9b9;
  max-width: 344px;
}
.ready__wrapper .btn__blc {
  grid-column: 2/3;
}
.ready__wrapper .btn__blc + p {
  grid-column: 1/2;
  font-size: 16px;
}
.ready .container {
  display: grid;
  grid-template-columns: 1fr 375px;
}
.ready .container img {
  width: 100%;
  height: 100%;
  min-height: 100%;
  object-fit: cover;
}

.footer {
  padding: 56px 0 250px;
  background: #1c1c1c;
}
.footer .container {
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
}
.footer__logo {
  max-width: 500px;
}
.footer__logo a {
  max-width: 148px;
  display: block;
}
.footer__logo a img {
  width: 100%;
}
.footer__logo p {
  margin: 40px 0 0;
  font-family: "Raleway", sans-serif;
  font-weight: 800;
  font-size: 40px;
  line-height: 1.2;
  color: #edde3f;
}
.footer__list {
  display: grid;
  grid-template-columns: 1fr;
  gap: 40px;
}
.footer__list a {
  font-family: "Raleway", sans-serif;
  font-weight: 600;
  font-size: 20px;
  line-height: 1.2;
  color: #fff;
  transition: all 0.3s;
}
.footer__list a:hover {
  color: #edde3f;
}
.footer__info {
  max-width: 457px;
  margin: 0 0 0 auto;
}
.footer__info p {
  margin: 24px 0 0;
  font-family: "Manrope", sans-serif;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.22222;
  color: #fff;
  max-width: 400px;
}
.footer__info p a {
  color: #edde3f;
  transition: all 0.3s;
}
.footer__info p a:hover {
  opacity: 0.8;
}
.footer__phone {
  font-family: "Manrope", sans-serif;
  font-weight: 800;
  font-size: 40px;
  line-height: 1.2;
  color: #fff;
  transition: all 0.3s;
}
.footer__phone:hover {
  color: #edde3f;
}
.footer__soc {
  margin: 24px 0 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 18px;
}
.footer__soc a {
  display: block;
  transition: all 0.3s;
}
.footer__soc a:hover {
  opacity: 0.8;
}
.footer__soc svg {
  width: 40px;
}

@media (max-width: 1400px) {
  .btn__wht, .btn__blc {
    padding: 15px 25px;
    font-size: 18px;
  }
  .nav__list {
    gap: 30px;
  }
  .nav__logo {
    max-width: 100px;
  }
  .nav__list a {
    font-size: 18px;
  }
  .nav .container {
    gap: 15px;
  }
  .founder__main {
    max-width: 100%;
  }
  .founder__main h2, .founder__main_text {
    max-width: 50%;
    margin: 0 0 0 auto;
  }
  .founder__img {
    left: 0;
  }
  .why__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .work__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .work__info {
    max-width: 50%;
  }
  .work__bg {
    right: 0;
    left: auto;
  }
  .form__wrapper {
    grid-template-columns: 1fr;
  }
  .plan__wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
  .plan__item_start {
    padding: 20px 10px;
    grid-column: 1/4;
  }
  .task__wrapper {
    grid-template-columns: 1fr 1fr;
    row-gap: 80px;
  }
  .rate__wrapper {
    grid-template-columns: 1fr 1fr;
  }
}
@media (max-width: 1000px) {
  .nav {
    padding: 10px 0 30px;
    box-shadow: 0 4px 4px -1px rgba(12, 12, 13, 0.05), 0 4px 4px -1px rgba(12, 12, 13, 0.1);
  }
  .nav .container {
    flex-wrap: wrap;
    row-gap: 0;
  }
  .nav__list,
  .nav .btn__wht, .nav__soc {
    display: none;
  }
  .nav__hum {
    margin: 0 0 0 auto;
    display: block;
    position: relative;
    width: 42px;
    height: 30px;
    cursor: pointer;
  }
  .nav__hum::before, .nav__hum::after,
  .nav__hum span {
    content: "";
    display: block;
    width: 100%;
    height: 4px;
    border-radius: 3px;
    background: #edde3f;
    transition: all 0.3s;
    left: 0;
    top: 0;
    position: absolute;
  }
  .nav__hum span {
    top: 50%;
    transform: translateY(-50%);
  }
  .nav__hum::after {
    top: auto;
    bottom: 0;
  }
  .nav.active {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow-y: auto;
    z-index: 50;
  }
  .nav.active .btn__wht {
    display: block;
    margin: 26px 0 0;
    width: 100%;
    max-width: 100%;
    background: transparent;
    color: #fff;
  }
  .nav.active .nav__hum span {
    display: none;
  }
  .nav.active .nav__hum::before, .nav.active .nav__hum::after {
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(45deg);
  }
  .nav.active .nav__hum::after {
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .nav.active .nav__list {
    margin: 34px 0 0;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 36px;
  }
  .nav.active .nav__list a {
    font-size: 20px;
  }
  .nav.active .nav__phone {
    margin: 34px 0 0;
    display: block;
    width: 100%;
    font-family: "Manrope", sans-serif;
    font-weight: 800;
    font-size: 30px;
    line-height: 1.86667;
    color: #fff;
    text-align: center;
  }
  .nav.active .nav__soc {
    display: flex;
    margin: 16px 0 0;
    justify-content: center;
    width: 100%;
  }
  .nav.active .nav__soc a {
    display: block;
  }
  .header {
    position: relative;
    padding: 10px 0 67px;
  }
  .header__wrapper {
    display: block;
  }
  .header__wrapper::before {
    background: linear-gradient(180deg, rgba(28, 28, 28, 0.9) 10%, rgba(28, 28, 28, 0) 60%);
    content: "";
    display: block;
    width: 100vw;
    height: 200px;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    position: absolute;
    z-index: -1;
  }
  .header__wrapper img {
    position: absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -2;
  }
  .header h1 {
    font-size: 24px;
    color: #fff;
  }
  .header h1 span {
    font-size: inherit;
  }
  .header p {
    margin: 200px 0 0;
    position: relative;
    font-size: 16px;
    color: #fff;
    padding: 13px 0 4px;
    z-index: 2;
  }
  .header p::before {
    content: "";
    display: block;
    width: 100vw;
    height: 100%;
    left: 50%;
    top: 0;
    transform: translateX(-50%);
    position: absolute;
    background: rgba(28, 28, 28, 0.6);
    z-index: -1;
  }
  .header__btn {
    margin: 24px 0 0;
  }
  .header .btn__blc {
    color: #1c1c1c;
    background: #edde3f;
  }
  .header .btn__wht {
    background: #1c1c1c;
    color: #fff;
  }
  .founder__main {
    padding: 80px 0 0;
  }
  .founder__main h2, .founder__main_text {
    max-width: 100%;
    margin: 0;
  }
  .founder__main h2 {
    font-size: 24px;
    max-width: 343px;
  }
  .founder__main_text {
    margin: 497px 0 0;
  }
  .founder__main_text p {
    font-size: 16px;
  }
  .founder__img {
    top: 165px;
    padding: 35px 100px 35px 0;
  }
  .founder__img img {
    width: 290px;
  }
  .founder__har {
    grid-template-columns: 1fr 1fr;
  }
  .founder__item {
    grid-column: auto;
    padding: 50px 10px 30px;
  }
  .founder__item ul {
    grid-template-columns: 1fr;
  }
  .result__wrapper {
    grid-template-columns: 1fr 1fr;
  }
  .work__bg {
    display: none;
  }
  .work__info {
    max-width: 100%;
  }
  .help__title::after, .help__title::before, .help__wrapper::after, .help__wrapper::before {
    content: none;
  }
  .help__wrapper {
    padding: 0;
    gap: 20px;
    width: 100%;
    max-width: 100%;
  }
  .help__btn p {
    font-size: 20px;
    color: #1c1c1c;
  }
  .help__info {
    position: relative;
    inset: auto;
  }
  .help__info h3 {
    display: none;
  }
  .help__info p {
    font-size: 14px;
    margin: 0;
  }
  .help__info .btn__blc {
    display: none;
  }
}